<template>
  <div>

    <v-divider></v-divider>

    <div
    class="pa-4"
    >

      <v-form
        ref="form"
      >
        <v-text-field
        v-model="object.nome"
        label="Name"
        outlined
        ></v-text-field>

        <v-text-field
        v-model="object.email"
        label="E-mail"
        outlined
        ></v-text-field>

        <v-text-field
        v-model="object.telefone"
        label="Telefone"
        outlined
        ></v-text-field>

        <v-text-field
        v-model="object.empresa"
        label="Empresa"
        v-on:salvar-object="$emit"
        outlined
        ></v-text-field>

      </v-form>

    </div>
    <div
    class="d-flex justify-end"
    >
      <ButtonCancel 
      />
      <div
      @click="salvar()"
      >
        <ButtonConclude />
      </div>
    </div>
    </div>

</template>

<script>
import ButtonCancel from '../../_System/_Buttons/ButtonCancel.vue'
import ButtonConclude from '../../_System/_Buttons/ButtonConclude.vue'
const axios = require('axios').default;

export default {
    components:{ButtonCancel, ButtonConclude},
    data(){
      return{
        object : {
          title: '',
          subtitle: '',
          nome:'',
          email:'',
          telefone:'',
          empresa:''
        }
      }
    },
    methods:{
      async salvar(){
        this.object.title = this.object.nome;
        this.object.subtitle = this.object.empresa;

        await axios({
          method: 'put',
          url: 'http://localhost:3000/leads',
          data: this.object
        }).then(()=>{
          this.$router.push({name: this.$store.state.exibicaoDoObjeto.class})
        })
      }
    }
}
</script>

